import {
  Component,
  CUSTOM_ELEMENTS_SCHEMA,
  HostBinding,
  Input,
  NO_ERRORS_SCHEMA,
  OnChanges,
  OnInit,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { NgClass, NgIf } from '@angular/common';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';

export type MaterialButtonVariant = 'icon-button' | 'unelevated-button' | 'outlined-button' | 'button-base';

export type DscButtonVariant = 'primary'
                             | 'secondary'
                             | 'danger'
                             | 'secondary-outlined'
                             | 'danger-outlined'
                             | 'auxiliary'
                             | 'secondary-text'
                             | 'secondary-text-inverse'
                             | 'danger-text'
                             | undefined;

export type DscButtonSize = 'large' | 'standard' | 'small';

export type DscButtonType = 'button' | 'submit';

@Component({
  selector: 'dsc-button',
  standalone: true,
  encapsulation: ViewEncapsulation.None,
  imports: [MatButtonModule, MatIconModule, NgClass, NgIf],
  templateUrl: './dsc-button.component.html',
  styleUrls: ['./dsc-button.component.scss'],
  schemas: [CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA]
})
export class DscButtonComponent implements OnInit, OnChanges {

  @Input() type: DscButtonType = 'button';

  @Input() label?: string;

  @Input() iconSuffix?: string;

  @Input() iconPrefix?: string;

  @Input() icon?: string;

  @Input() tabIndex: number = 0;

  @Input() dscTooltip?: string;

  @Input() ariaLabel?: string;

  @Input() iconStyle: 'outlined' | 'filled' | 'rounded' | 'sharp' | 'two-tone' = 'filled';

  @Input()
  get variant(): DscButtonVariant {
    return this._variant;
  }

  set variant(value: DscButtonVariant) {
    this._variant = value;
  }

  private _variant: DscButtonVariant = 'primary';

  @Input()
  get size(): any {
    return this._size;
  }

  set size(value: DscButtonSize) {
    this.sizeIcon = `mat-mdc-icon-button--${value || 'standard'}`;
    this._size = `mdc-button--${value || 'standard'}`;
  }

  private _size = 'mdc-button--standard';

  sizeIcon = 'mat-mdc-icon-button--standard'

  @Input()
  get disabled(): boolean {
    return this._disabled;
  }

  set disabled(value: boolean | string) {
    this._disabled = coerceBooleanProperty(value);
  }

  private _disabled = false;

  @HostBinding('attr.disabled')
  get isDisabled() {
    return this.disabled ? true : null;
  }

  @Input()
  get iconButton(): boolean {
    return this._iconButton;
  }

  set iconButton(value: BooleanInput) {
    this._iconButton = coerceBooleanProperty(value);
  }

  private _iconButton = false;

  variantButtonClass!: string;

  pressedByEnter = false;

  ngOnInit(): void {
    this._setVariantButtonClass(this.variant);
  }
 
  onKeyDown(event: KeyboardEvent): void {
    if (event.key === 'Enter') {
      this.pressedByEnter = true;
    }
  }

  onKeyUp(event: KeyboardEvent): void {
    if (event.key === 'Enter') {
      this.pressedByEnter = false;
    }
  }


  private _setVariantButtonClass(value: DscButtonVariant): void {
    let materialButtonVariant: MaterialButtonVariant = 'icon-button';

    if (!this.iconButton) {
      switch (value) {
        case 'primary':
        case'secondary':
        case 'danger':
          materialButtonVariant = 'unelevated-button';
          break;
        case 'secondary-outlined':
        case 'danger-outlined':
        case 'auxiliary':
          materialButtonVariant = 'outlined-button';
          break;
        case 'secondary-text':
        case 'secondary-text-inverse':
        case 'danger-text':
          materialButtonVariant = 'button-base';
          break;
        default:
          materialButtonVariant = 'unelevated-button';
          break;
      }
    }

    this.variantButtonClass = `mat-mdc-${materialButtonVariant}--${this._variant}`;
  }

  ngOnChanges(changes: SimpleChanges): void {
    for(const propName in changes) {
      if (changes.hasOwnProperty(propName)) {
        switch (propName) {
          case 'iconButton':
          case 'variant':
            this._setVariantButtonClass(this.variant);
            break;
        }
      }
    }
  }

  isImage(icon: any): boolean {
    return icon?.toLowerCase().endsWith('.svg') || icon?.toLowerCase().endsWith('.png');
  }

  getIconClass(): string {
    if (this.iconStyle === 'filled') return 'material-icons';
    if (this.iconStyle === 'rounded') return 'material-icons-round';
    return `material-icons-${this.iconStyle}`;
  }
}
